* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #fff;
  color: #d91e18;
  font-family: Helvetica, Arial, sans-serif;
  text-align: center;
}

html,
body,
.pomodori {
  height: 100%;
}

.hidden {
  display: none;
}

.block {
  display: block;
}

.center {
  margin: 0 auto;
  text-align: center;
}

.symbol {
  fill: white;
}

.timer {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.timer-time {
  font-size: 25vmin;
  font-weight: 200;
}

.timer-toggle {
  background: transparent;
  border: 0;
  color: #d91e18;
  cursor: pointer;
  font-size: 7vmin;
  letter-spacing: 3;
  text-transform: uppercase;
}

input {
  border: 1px solid rgba(0, 0, 0, .4);
  padding: 10px;
}